<template>
    <div class="order" style="line-height:25px">
        <el-row>
            <el-col :span="21">
                <!-- 模糊检索 -->
                <div class="grid-content bg-purple">
                    <el-form :inline="true" :model="orderFormLike" class="demo-form-inline">
                        <el-form-item label="乘坐人">
                            <el-input v-model="orderFormLike.passengers" placeholder="请输入乘坐人" clearable></el-input>
                        </el-form-item>
                        <el-form-item label="出发城市">
                            <el-input v-model="orderFormLike.departureCity" placeholder="请输入出发城市" clearable></el-input>
                        </el-form-item>
                        <el-form-item label="到达城市">
                            <el-input v-model="orderFormLike.arriveCity" placeholder="请输入到达城市" clearable></el-input>
                        </el-form-item>
                        <el-form-item>
                            <el-button type="primary" @click="search">查询</el-button>
                        </el-form-item>
                    </el-form>
                </div>
            </el-col>
            <el-col :span="3">
                <div class="grid-content bg-purple-light">
                    <el-button type="primary" @click="insert">新增</el-button>
                </div>
            </el-col>
        </el-row>
        
        <!--功能列表-->
        <el-row>
            <el-table :data="tableData" :border="true" class="tableBox" style="width: 100%">
                <el-table-column prop="passengers" label="乘坐人" ></el-table-column>
                <el-table-column prop="departureCity" label="出发城市" ></el-table-column>
                <el-table-column prop="arriveCity" label="到达城市" ></el-table-column>
                <el-table-column prop="price" label="价格" ></el-table-column>
                <el-table-column prop="orderDetails" label="订单详情" ></el-table-column>
                <el-table-column prop="startTime" label="开始时间" ></el-table-column>  
                <el-table-column prop="endTime" label="结束时间" ></el-table-column>  

                <el-table-column width="300" label="操作">
                    <template slot-scope="scope">
                        <el-button @click="toEditPro(scope.row)" size="small">编辑</el-button>
                        <el-button @click="toDelOrder(scope.row)" type="danger" size="small">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-row>

        <!--分页功能-->
        <el-row>
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="[5, 10, 20, 100, 200, 300, 400]"
                :page-size="pageSize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total"
            >
            </el-pagination>
        </el-row>

        <!-- 新增或修改模态框 -->
        <el-row>
            <el-dialog title="订单管理" :visible.sync="editProState">
                <el-form :model="orderForm">
                    <el-form-item label="乘坐人">
                        <el-input v-model="orderForm.passengers" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="出发城市">
                        <el-input v-model="orderForm.departureCity" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="到达城市">
                        <el-input v-model="orderForm.arriveCity" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="价格">
                        <el-input v-model="orderForm.price" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="订单详情">
                        <el-input v-model="orderForm.orderDetails" auto-complete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="开始时间">
                        <el-date-picker v-model="orderForm.startTime" type="datetime" value-format="yyyy-MM-dd HH:mm:ss" placeholder="选择日期时间"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="结束时间">
                        <el-date-picker v-model="orderForm.endTime" type="datetime" value-format="yyyy-MM-dd HH:mm:ss" placeholder="选择日期时间"></el-date-picker>
                    </el-form-item>
                </el-form>
                <!-- 按钮 -->
                <div slot="footer" class="dialog-footer">
                    <el-button @click="editProState = false">取 消</el-button>
                    <el-button type="primary" @click="saveInfo">确 定</el-button>
                </div>
            </el-dialog>
        </el-row>

    </div>
</template>

<script>

  export default {
    data() {
      return {
        orderFormLike:{
            passengers: '',
            departureCity: '',
            arriveCity: ''
        },
        tableData: [],
        currentPage: 1,
        pageSize: 5,
        total: 100,
        orderForm: {
            passengers: '',
            departureCity: '',
            arriveCity: '',
            price: '',
            orderDetails: '',
            startTime: '',
            endTime: ''
        },
        editProState: false
      }
    },
    created: function(){
        this.load();  //页面初始化时，加载的方法
    },
    methods: {
        //初始化数据
        load() {
            let {passengers,departureCity,arriveCity} = this.orderFormLike;
            var _this = this;
            this.$http({
                method: 'post',
                url: '/Order/getPage',
                data: {pageCode:this.currentPage,pageSize:this.pageSize,passengers:passengers,departureCity:departureCity,arriveCity:arriveCity}
            }).then(res => {
                if (res.status == 200) {
                    _this.tableData = res.data.list;
                    _this.total = res.data.total;
                }
                
            });
        },
        //模糊检索
        search() {
            this.load();
        },
        //每页显示数量发生变化
        handleSizeChange(val) {
            this.pageSize = val;
            this.load();
        },
        //第几页发生变化
        handleCurrentChange(val) {
            this.currentPage = val;
            this.load();
        },
        //点击新增按钮
        insert(){
            this.orderForm = {};
            this.editProState = true;
        },
        //点击编辑按钮
        toEditPro(val) {
            let id = val.id;
            this.$http({
                method: 'get',
                url: '/Order/getById',
                params: {
                    id:id
                }
            }).then(res => {
                if(res.data.code == 200){
                    this.orderForm = res.data.data;
                    this.editProState = true;
                }
            });
        },
        //新增或修改表单保存
        saveInfo(){
            if(this.orderForm.id != null){
                //修改
                this.$http({
                    method: 'post',
                    url: '/Order/updateById',
                    data: this.orderForm
                }).then(res => {
                    let code = res.data.code;
                    if(code == 200){
                        this.editProState = false;
                        this.load();
                        this.$message.success({
                            message: res.data.msg
                        })
                    }else{
                        this.$message.error({
                            message: res.data.msg
                        })
                    }
                });
            }else{
                //新增
                this.$http({
                    method: 'post',
                    url: '/Order/add',
                    data:this.orderForm
                }).then(res => {
                    let code = res.data.code;
                    if(code == 200){
                        this.editProState = false;
                        this.load();
                        this.$message.success({
                            message: res.data.msg
                        })
                    }else{
                        this.$message.error({
                            message: res.data.msg
                        })
                    }
                });
            }
        },
        //删除
        toDelOrder(val){
            this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
                confirmButtonText: "确定",
                cancelButtonText: "取消",
                type: "warning",
            }).then(() => {
                let id = val.id;
                this.$http({
                    method: 'get',
                    url: '/Order/delete',
                    params: {
                        id:id
                    }
                }).then(res => {
                    if(res.data.code == 200){
                        this.load();
                        this.$message.success({
                            message: res.data.msg
                        })
                    }else{
                        this.$message.error({
                            message: res.data.msg
                        })
                    }
                });
            });
        }
    }
  }
</script>

<style>
  .bg-purple{
      text-align: left;
  }
  .bg-purple-light{
      text-align: right;
  }
  .el-date-editor.el-input{
    width: 100%;
  }
</style>